export default {
	data() {
		return {
			face:"../../../static/img/face.jpg",
			nickname:'游客1001',
			signature:'',
			isfirst:true,
			headerPosition:"fixed",
			headerTop:null,
			statusTop:null,
			showHeader:true,
			//个人信息,
			user:{
				face:'',
				// signature:'点击昵称跳转登录/注册页',
				integral:0,
				balance:0,
				envelope:0
			},
			// 订单类型
			orderList:[
				{text:'待付款',icon:"fukuan"},
				{text:'待发货',icon:"fahuo"},
				{text:'待收货',icon:"shouhuo"},
				{text:'待评价',icon:"pingjia"},
				// {text:'退货',icon:"tuihuo"}
			],
			// 工具栏列表
			mytoolbarList:[
				{url:'../../user/keep/keep',text:'我的收藏',img:'/static/img/user/point.png'},
				{url:'../../user/coupon/coupon',text:'我的关注',img:'/static/img/user/quan.png'}, 
				// {url:'',text:'评论',img:'/static/img/user/renw.png'},
				// {url:'',text:'领红包',img:'/static/img/user/momey.png'},
				
				{url:'../../user/address/address',text:'收货地址',img:'/static/img/user/addr.png'},
				// {url:'',text:'账户安全',img:'/static/img/user/security.png'},
				// {url:'',text:'银行卡',img:'/static/img/user/bank.png'},
				// {url:'',text:'抽奖',img:'/static/img/user/choujiang.png'},
				// {text:'客服',img:'/static/img/user/kefu.png'},
				// {text:'签到',img:'/static/img/user/mingxi.png'}
				
			]
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	onLoad() {
		this.myseleif();
		this.statusHeight = 0;




		
	},
	onReady(){
		this.myseleif();
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function () {
			},
			fail:function(e){
			}
		});
	},
	onShow(){
		this.myseleif();
		uni.getStorage({
			key: 'UserInfo',
			success: (res)=>{
				if(!res.data){
					if(this.isfirst){
						//this.toLogin();
					}
					return ;
				}
				this.user = res.data;
			},
			fail:(e)=>{
				//this.toLogin(); 
			}
		})
	},
	methods: {
		myseleif(){
			//获取用户的头像
			uni.getStorage({
				key:"face",
				success:res=>{
					// console.log(res.data[0]);
					this.face=res.data[0];
					// return res.data[0];
					console.log(this.face.substring(2))
				},fail: res => {
					this.face="../../../static/img/face.jpg"
				}
			}),
			// 获取昵称
			uni.getStorage({
				key:'rename',
				success:res=>{
					this.nickname=res.data;		
					console.log("成功获取rename"+this.nickname);
				},fail: res => {
					this.nickname="蓝泽邀请你修改昵称"
				}
			});
			// 获取个性签名
			uni.getStorage({
				key:'signature',
				success:res=>{
					this.signature=res.data;		
					console.log("成功获取signature"+this.signature);
				},fail: res => {
					this.signature="一路同行，感谢有你"
				}
			});
		},
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../../msg/msg'
			})
		},
		toOrderList(index){
			uni.setStorageSync('tbIndex',index);
			uni.navigateTo({url:'../../user/order_list/order_list?tbIndex='+index}) 
		},
		toSetting(){
			uni.navigateTo({
				url:'../../user/setting/setting'
			})
		},
		toMyQR(){
			uni.navigateTo({
				url:'../../user/myQR/myQR'
			})
		},
		toLogin(){
			uni.showToast({title: '请登录',icon:"none"});
			uni.navigateTo({
				url:'../../login/login'
			})
			this.isfirst = false;
		},
		isLogin(){
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false
		},
		toDeposit(){
			uni.navigateTo({
				url:'../../user/deposit/deposit'
			})
		},
		toPage(url){
			if(!url){
				uni.showToast({title: '模板未包含此页面',icon:"none"});return;
			}
			uni.navigateTo({
				url:url
			})
		},
		undo(){
			uni.showToast({
				title:"业务功能完善中，敬请期待",
				icon:"none"
			})
		}
	}
} 
